import React from "react"
import imgRedes from '../../../images/redes.jpeg'
import imgPublicitarios from '../../../images/publicitarios.jpeg'
import imgMarketing from '../../../images/marketing.jpeg'
import imgPaginas from '../../../images/paginas-web.jpeg'
const ServicesData =[
    {
        nombre:"redes sociales",
        desc:"Impulsamos a tu negocio a crecer con las diferentes técnicas de marketing digital que nosotros empleamos brindándote la mayor calidad y un servicio sumamente profesional en Facebook, Instagram, Google ente otras.",
        img:imgRedes,
        atl:"Imagen haciendo referencia de un celular haciendo referencia a laas redes sociales"
    },
    {
        nombre:"diseños publicitarios",
        desc:"Somos creatividad, creamos los diseños mas llamativos e ilustrativos con alto crecimiento para tu empresa u negocio ofreciéndote los mejores logos, catálogos y revistas, diseño de paginas web, poster, flyer y entre otros.",
        img:imgPublicitarios,
        alt:"Imagen de espaectacular editada con las muestra de un posible espectacular"
    },
    {
        nombre:"campañas publicitarias",
        desc:"Creamos para ti y desarrollamos tu segmentación para que tengas mayores probabilidades de éxito en tus anuncios tomando la experiencia y consideraciones por nuestros profesionales.",
        img:imgMarketing,
        alt:"Megafono referenciando a la divulgacion por medio online"
    },
    {
        nombre:"paginas web",
        desc:"Desarrollamos tu identidad digital para facilitar la atraccion de clientes nuevos y estar a la vanguardia en el oceano online.",
        img:imgPaginas,
        alt:"Laptop y celular mostrando una pagina web adatapda a ambos dispositivos"
    },
]

export const renderServices = ()=>{
    let templateBase =[]
    ServicesData.map((service,i)=>{
        templateBase.push(<Service service={service} count={i}/>)
    })
    return templateBase
}

const Service = ({service,count})=>{
    const order = ()=>{
        if(!(count%2)){
            return {
                style:{},
                effect:"fade-left",
                textAlign:"text-md-start"
            }
        }else{
            return { 
                style:{
                    flexDirection:"row-reverse" 
                },
                effect:"fade-right",
                textAlign:"text-md-end"
            }
        }
    }

    return(
        <article className="row mt-5 w-100 mx-auto py-5" data-aos={"fade-left"} data-aos-delay="300" style={order().style}>
            <div className={`col-md-6 px-5 text-center ${order().textAlign}`} style={{ display:"flex",flexDirection:"column", alignContent:"space-between" }}>
                <h3 className="text-uppercase fs-2">{service.nombre}</h3>
                <p>{service.desc}</p>
                {/*<a className="py-2 px-4" children="Leer más"/>*/}
            </div>
            <div className="col-md-6 px-5">
                <div className="w-100" style={{height:"17rem",borderRadius:"15px"}}>
                    <img className="w-100 h-100" src={service.img} alt={service.alt} style={{boxShadow:" 0 0 20px black"}}/>
                </div>
            </div>
        </article>
    )
}