import { Container, CloseBtn, NavLink } from "./mobileMenu-styles"


const MenuMobile = (props) => {
    const { show, changeState} = props
    const setState=()=>{
        changeState(!show)
    }
    return<>
        <Container className="d-flex d-md-none" show={show}>
            <CloseBtn children={"X"} onClick={()=>setState()}/>
                <NavLink className="d-block w-100 text-center" href="">Link</NavLink>
                <NavLink className="d-block w-100 text-center" href="">Link</NavLink>
        </Container>
    </>    
}

export default MenuMobile